.todo {
  border-bottom: 1px solid #e0e9f4;
  padding: 1.5em 2em;
}

.todo label {
  display: flex;
  align-items: center;
  gap: 1.5em;
  position: relative;
}

input[type="checkbox"] {
  position: relative;
  cursor: pointer;
  visibility: hidden;
}

input[type="checkbox"]:checked + .checkbox {
  background-color: #ff7f59;
  border: none;
}

input[type="checkbox"]:checked + .checkbox > .checkbox-image {
  opacity: 1;
}

.checkbox {
  width: 1rem;
  height: 1rem;
  border-radius: 2px;
  border: 1px solid #90a3bf;
  opacity: 1;
  position: absolute;
  cursor: pointer;
}

.checkbox-image {
  width: 100%;
  height: 100%;
  background: url("../images/icons8-done.svg") no-repeat center center/cover;
  opacity: 0;
  transition: opacity 50ms;
}

.todo-task {
  color: #596780;
}

.todo-task__finished {
  text-decoration: line-through;
}

.todo__delete {
  color: #ff7f59;
  font-size: 1.25rem;
  cursor: pointer;
  margin-left: auto;
}
