.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(213, 48%, 98%);
  padding: 1em;
  border-bottom: 1px solid #e0e9f4;
}

.header-date__day,
.header-date__date {
  font-size: 1.953rem;
  color: #7c5cfc;
}

.header-date__day {
  font-weight: 400;
}

.header-date__date {
  font-weight: 300;
}

.header-date__month,
.header-tasks {
  font-weight: 300;
  color: #90a3bf;
}

.header-tasks__number {
  font-weight: 400;
  color: #596780;
}
