.todo-form {
  padding: 1.5em 1em 2em;
  border-bottom: 1px solid #ffe7d3;
}

.todo-form label {
  display: block;
  color: #596780;
  font-size: 1.25rem;
}

.todo-form__input {
  padding: 0.5em;
  width: 14em;
  margin-top: 1em;
  margin-right: 2em;
  border: none;
  border-bottom: 1px solid #e0e9f4;
  font-family: inherit;
  font-size: 1rem;
  color: #596780;
}

.todo-form__input::placeholder {
  color: #c3d4e9;
}

.todo-form__input:focus {
  outline: none;
  border-bottom-color: #ff7f59;
}

.add-task-btn {
  background: #ff7f59;
  color: #fff;
  border: none;
  font-size: 1rem;
  padding: 0.5em 1em;
  cursor: pointer;
  border-radius: 2px;
}
